.greetingForm {
    width: 500px;
}

.inputAndButtonContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.error {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #c00;
}

.input {
  border: 1px solid #d1d1d1;
  border-radius: 5px;
  width: 372px;
  height: 36px;
}

.input:focus {
outline: none;
}

.errorInput {
  border: 1px solid #c00;
}

.button {
